html {
  overflow-y: scroll; }

body {
  padding-top: 100px; }
  @media (min-width: 992px) {
    body {
      padding-top: 110px; } }

@media (min-width: 992px) {
  .sidebar {
    position: fixed;
    top: 100px;
    bottom: 0;
    overflow-y: auto; }
    .sidebar.sidebar-left {
      left: 0; }
    .sidebar.sidebar-right {
      right: 0; } }

.sidebar h6 {
  color: #555; }

.sidebar .nav-link {
  padding: .3rem 1rem .3rem 0;
  font-size: 16px; }
  .sidebar .nav-link.active {
    color: #da3458; }

.card-header .nav-tabs {
  border: 0; }
  .card-header .nav-tabs .nav-link {
    border: 0;
    padding: .3rem 1rem .3rem 0;
    font-size: 16px; }

pre[class*="language-"],
code[class*="language-"] {
  color: #d4d4d4;
  font-size: 13px;
  text-shadow: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none; }

pre[class*="language-"]::selection,
code[class*="language-"]::selection {
  text-shadow: none;
  background: #b3d4fc; }

@media print {
  pre[class*="language-"],
  code[class*="language-"] {
    text-shadow: none; } }

pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
  background: #232323; }

:not(pre) > code[class*="language-"] {
  padding: .1em .3em;
  border-radius: .3em;
  color: #db4c69;
  background: #f9f2f4; }

/*********************************************************
* Tokens
*/
.namespace {
  opacity: .7; }

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #6a9955; }

.token.punctuation {
  color: #d4d4d4; }

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #b5cea8; }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #d69d85; }

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #dcdcdc;
  background: #232323; }

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #c586c0; }

.token.function {
  color: #dcdcaa; }

.token.regex,
.token.important,
.token.variable {
  color: #d16969; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.constant {
  color: #9CDCFE; }

.token.class-name {
  color: #4EC9B0; }

.token.parameter {
  color: #9CDCFE; }

.token.interpolation {
  color: #9CDCFE; }

.token.punctuation.interpolation-punctuation {
  color: #9b9b9b; }

.token.boolean {
  color: #dcdcdc; }

.token.property {
  color: #9cdcfe; }

.token.selector {
  color: #d7ba7d; }

.token.tag {
  color: #569cd6; }

.token.attr-name {
  color: #9cdcfe; }

.token.attr-value {
  color: #d69d85; }

.token.entity {
  color: #4ec9b0;
  cursor: unset; }

.token.namespace {
  color: #4ec9b0; }

/*********************************************************
* Language Specific
*/
pre[class*="language-javascript"],
code[class*="language-javascript"] {
  color: #4ec9b0; }

pre[class*="language-css"],
code[class*="language-css"] {
  color: #CE9178; }

pre[class*="language-html"],
code[class*="language-html"] {
  color: #dcdcdc; }

.language-html .token.punctuation {
  color: #9b9b9b; }

/*********************************************************
* Line highlighting
*/
pre[data-line] {
  position: relative; }

pre[class*="language-"] > code[class*="language-"] {
  position: relative;
  z-index: 1; }

.line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  padding: inherit 0;
  margin-top: 1em;
  background: #f7ebc6;
  box-shadow: inset 5px 0 0 #f7d87c;
  z-index: 0;
  pointer-events: none;
  line-height: inherit;
  white-space: pre; }

/* Copy button */
div.code-toolbar {
  position: relative; }

div.code-toolbar > .toolbar {
  position: absolute;
  top: .3em;
  right: .2em;
  transition: opacity 0.3s ease-in-out;
  opacity: 0; }

div.code-toolbar:hover > .toolbar {
  opacity: 1; }

div.code-toolbar > .toolbar .toolbar-item {
  display: inline-block; }

div.code-toolbar > .toolbar a {
  cursor: pointer; }

div.code-toolbar > .toolbar button {
  background: none;
  border: 0;
  color: inherit;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none; }

div.code-toolbar > .toolbar a,
div.code-toolbar > .toolbar button,
div.code-toolbar > .toolbar span {
  color: #bbb;
  font-size: .8em;
  padding: 0 .5em;
  background: #333;
  border-radius: .5em; }

div.code-toolbar > .toolbar a:hover,
div.code-toolbar > .toolbar a:focus,
div.code-toolbar > .toolbar button:hover,
div.code-toolbar > .toolbar button:focus,
div.code-toolbar > .toolbar span:hover,
div.code-toolbar > .toolbar span:focus {
  color: inherit;
  text-decoration: none; }

/* ==========================================================================
   Prism Syntax highlighter
   ========================================================================== */
pre[class*='language-'] {
  padding-top: 1rem;
  padding-bottom: 1rem; }

div.code-toolbar {
  margin-bottom: 0; }

div.code-toolbar > .toolbar {
  top: 0;
  right: 0;
  padding: 2px 5px 3px;
  opacity: 1 !important;
  background-color: #3c3f45;
  border: 1px solid #272e32;
  border-radius: 0 0 0 4px; }

div.code-toolbar > .toolbar .toolbar-item a,
div.code-toolbar > .toolbar .toolbar-item button,
div.code-toolbar > .toolbar .toolbar-item span {
  color: #eee;
  border-radius: 3px;
  background: none;
  box-shadow: none; }

div.code-toolbar > .toolbar .toolbar-item a:hover,
div.code-toolbar > .toolbar .toolbar-item button:hover,
div.code-toolbar > .toolbar .toolbar-item span:hover {
  color: #fff;
  border-radius: 3px;
  background: none;
  box-shadow: none; }

body, html {
  -webkit-overflow-scrolling: touch;
  -webkit-appearance: none; }

::-webkit-scrollbar-button:end:increment,
::-webkit-scrollbar-button:start:decrement {
  background: transparent;
  display: none; }

::-webkit-scrollbar-thumb:vertical {
  border-radius: 9pt;
  display: block;
  background-color: #dce0e4;
  border: 4px solid rgba(255, 255, 255, 0);
  background-clip: content-box; }
  ::-webkit-scrollbar-thumb:vertical:hover {
    background-color: #ccc; }

::-webkit-scrollbar-thumb:horizontal {
  border-radius: 9pt;
  display: block;
  background-color: rgba(255, 255, 255, 0.5);
  border: 5px solid rgba(255, 255, 255, 0);
  background-clip: content-box;
  transition: background-color .5s ease; }
  ::-webkit-scrollbar-thumb:horizontal:hover {
    background-color: rgba(255, 255, 255, 0.7); }

::-webkit-scrollbar {
  background-color: transparent;
  width: 14px; }
